import React, { Component } from "react";

export default class Task extends Component {
  constructor(props) {
    super(props);
    this.state = { buttonHover: false, taskHover: false, isCompleted: false };
    this.toggleButtonHover = this.toggleButtonHover.bind(this);
    this.toggleTaskHover = this.toggleTaskHover.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
  }

  toggleButtonHover() {
    this.setState({ buttonHover: !this.state.buttonHover });
  }

  toggleTaskHover() {
    this.setState({ taskHover: !this.state.taskHover });
  }

  handleClick() {
    this.setState({ isCompleted: !this.state.isCompleted });
  }

  handleDelete = (e) => {
    this.props.handleDelete(this.props.id);
  };

  render() {
    const taskstyle = {
      fontFamily: "Verdana",
      display: "flex",
      padding: "10px",
      flexFlow: "row",
      justifyContent: "center",
      alignItems: "center",
      fontSize: "24px",
    };
    const togglestyle = {
      marginLeft: "10px",
      cursor: "pointer",
    };

    // Default styling for Task
    var pstyle = { textDecoration: "none", margin: "0px" };

    // Styling on button hover
    if (this.state.buttonHover) {
      pstyle = { textDecoration: "line-through", margin: "0px" };
    }

    // Styling on button click
    if (this.state.isCompleted) {
      pstyle = { textDecoration: "linethrough", color: "gray", margin: "0px" };
    }

    // Styling on task hover????
    if (this.state.taskHover) {
      pstyle = { textDecoration: "underline", margin: "0px" };
    }

    // toggle button on click
    var toggleLine = <line
                      x1="5"
                      x2="15"
                      y1="10"
                      y2="10"
                      stroke="white"
                      strokeWidth="2"
                    ></line>;

    if (this.state.isCompleted) {
      toggleLine = <line
      x1="10"
      x2="10"
      y1="5"
      y2="15"
      stroke="white"
      strokeWidth="2"
    ></line>;
    }

    return (
      <div id="task" style={taskstyle}>
        <p
          style={pstyle}
          onMouseEnter={this.toggleTaskHover}
          onMouseLeave={this.toggleTaskHover}
        >
          {this.props.task}
        </p>
        <svg
          width="20px"
          height="20px"
          style={togglestyle}
          onMouseEnter={this.toggleButtonHover}
          onMouseLeave={this.toggleButtonHover}
          onClick={this.handleClick}
        >
          <circle cx="10" cy="10" r="10px" fill="gray"></circle>
          {toggleLine}
        </svg>
        <svg
          width="20px"
          height="20px"
          className="delete-task"
          onClick={this.handleDelete}
          style={togglestyle}
        >
          <circle cx="10" cy="10" r="10px" fill="red"></circle>
        </svg>
      </div>
    );
  }
}
